<template>
    <div>
        <div class="back">
            <div class="loginNav">
                <!-- <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns"> -->
                <p class="title f34 cfff text-center">我的</p>
            </div>
            <div class="myHead">
                <img :src="headImg" alt="" class="myImg">
                <div class="label f19 cfff">教师</div>
                <p class="userName text-center f30 cfff mt-33">{{name}}</p>
            </div>
        </div>
        <div class="setBox">
            <div class="setLine mt-30" @click="messageEdit">
                <div class="f26 setTitle">资料修改</div>
                <img class="setImg" src="../../assets/my/more.png" alt="">
            </div>
            <div class="setLine" @click="editPsw">
                <div class="f26 setTitle">修改密码</div>
                <img class="setImg" src="../../assets/my/more.png" alt="">
            </div>
            <div class="setLine" @click="setting">
                <div class="f26 setTitle">设置</div>
                <img class="setImg" src="../../assets/my/more.png" alt="">
            </div>
            <div class="setLine" @click="faceRecognition">
                <div class="f26 setTitle">人脸录入设置</div>
                <img class="setImg" src="../../assets/my/more.png" alt="">
            </div>
        </div>
        <van-tabbar v-model="active" class="tabBar">
            <van-tabbar-item @click="turnToIndex"><span> 首页</span><img
                slot="icon"
                slot-scope="props"
                :src="props.active ? icon.hmeActive : icon.home"
            ></van-tabbar-item>
            <van-tabbar-item @click="turnToMessage"><span> 消息</span><img
                slot="icon"
                slot-scope="props"
                :src="props.active ? icon.msgActive : icon.msg"
            ></van-tabbar-item>
            <van-tabbar-item><span> 我的</span><img
                slot="icon"
                slot-scope="props"
                :src="props.active ? icon.myActive : icon.my"
            > </van-tabbar-item>
        </van-tabbar>
    </div>
    
</template>

<script>
export default {
  name: 'HelloWorld',
    data () {
        return {
            name: '',
            headImg:'',
            active: 2,
            icon: {
                home: require('../../assets/my/home.png'),
                hmeActive: require('../../assets/my/homeActive.png'),
                msg:require('../../assets/my/msg.png'),
                msgActive:require('../../assets/my/msgActive.png'),
                my:require('../../assets/my/my.png'),
                myActive:require('../../assets/my/myActive.png')
            }
        }
    },
    methods : {
        messageEdit(){
            this.$router.push({path:'/messageEdit'})
        },
        editPsw(){
            this.$router.push({path:'/lostPsw/edit'})
        },
        setting(){
             this.$router.push({path:'/setting'})
        },
        turnToMessage(){
            this.$router.push({path:'/message'})
        },
        turnToIndex(){
            this.$router.push({path:'/indexs'})
        },
        faceRecognition() {
            var that = this;
            var arr = ['1',JSON.parse(localStorage.getItem('zhxy_user')).id.toString(),'2','2',JSON.parse(localStorage.getItem('zhxy_user')).token.toString()];
            console.log(arr)
            Cordova.exec(that.successFunction, that.failFunction, "Plugin", "faceRecognition", arr);//第一个参数 type 1录入2识别 第二个参数 id , 第三个参数 1学生 2老师 3家长 ，1登录识别，第四个参数 是否是登录 1登录2不是登录
        },
        successFunction(result){
            this.$router.push({path:'/my'});
        },
        failFunction(msg){
        },
        successCallback(msg){
            if(msg == 0){
                this.$router.push({path:'/indexs'})
            }else{
                alert('失败');
            }
        }
    },
    created() {
        var userMsg = JSON.parse(localStorage.getItem('zhxy_user'));
        this.name = userMsg.username;
        this.headImg = this.$api.fileUrl +  userMsg.headImage;
    }
}
</script>

<style scoped>
   .back{background: url('../../assets/my/myback.png') no-repeat;width: 100%;height: 4.46rem;background-size:100% 100%; }
   .title{font-weight: bold;}
   .myHead{width: 1.8rem;height: 1.8rem;border-radius: 50%;margin: 0.38rem auto 0;position: relative;}
   .myImg{width: 100%;height: 100%;display: block;border-radius: 50%;}
   .label{background: #7c88ee;border-radius: 50%;width: 0.5rem;height: 0.5rem;position: absolute;bottom: -0.16rem;right: 0.16rem;line-height: 0.5rem;text-align: center}
   .setBox{width: 100%;overflow: hidden;padding-bottom: 1rem;}
   .setLine{margin-left: 0.3rem;border-bottom: 1px solid #f2f2f2;overflow: hidden;padding: 0.37rem 0;}
   .setTitle{float: left;width: 5rem !important;}
   .setImg{width: 0.3rem;height: 0.3rem;display: block;float: right;margin-right: 0.3rem;}
   /* 修改组件样式 */
    .van-tabbar-item--active {color: #8a9cff;}
    .tabBar{position: fixed;bottom: 0;left: 0;}
    /* .aa,.bb{background: url('../../assets/login/erha.jpg')} */
</style>


